<template>
  <div class="max">
    <main class="max">
      <section class="padre max">
        <h2><router-link to="/home/"> Home </router-link> // Reportes //</h2>
        <div class="contenedor">
          <div class="form-search">
            <div class="menu-op">
              <select name="type_selected" class="menu" v-model="type_reports">
                <option value="" selected disabled hidden>Productos</option>
                <option value="productos" selected="selected">Productos</option>
                <option value="clientes">Clientes</option>
                <option value="ventas">Ventas</option>
                <option value="maquinas">Maquinas</option>
                <option value="empleados">Empleados</option>
              </select>
            </div>

            <div class="in-date">
              <p class="text">Inicia:</p>
              <input type="date" class="fecha" v-model="startDate" />
              <p class="text">Termina:</p>
              <input type="date" class="fecha" v-model="endDate" />
            </div>

            <div class="b-search">
              <button class="btn-b" @click="getReportsSelector(type_reports)">
                Buscar
              </button>
            </div>
          </div>
          <spinner-loader v-if="isLoading" :loading="isLoading" />
          <div class="container-table" v-if="!isLoading">
            <table id="tablita" class="tabla">
              <thead class="head">
                <tr class="head_row">
                  <th class="a-cel15">Clave</th>
                  <th class="a-cel20">Tipo</th>
                  <th class="a-cel25">Descripcion</th>
                  <th class="a-cel20">Fecha</th>
                  <th class="a-cel20">Boton</th>
                </tr>
              </thead>

              <tbody class="body">
                <tr v-for="reports in searchDate" :key="reports._id">
                  <td>{{ reports._id }}</td>
                  <td>{{ reports.type }}</td>
                  <td>{{ reports.description }}</td>
                  <td>{{ reports.fecha | date }}</td>

                  <td>
                    <button
                      class="b-editar"
                      @click="
                        imprimir(
                          reports.type,
                          reports.array,
                          reports.description
                        )
                      "
                    >
                      Imprimir
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>

            <p v-if="searchDate.length < 1" class="centerTextNotFound">
              <i>No se encontraron coincidencias</i>
            </p>
          </div>
          <div class="b-print">
            <button class="btn-c" @click="printFull(type_reports)">
              Imprimir tabla
            </button>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>
<script>
import axios from "axios";
import spinnerLoader from "./SpinnerLoader.vue";
import jsPDF from "jspdf";
import "jspdf-autotable";
import moment from "moment";
export default {
  name: "BodyReports",
  components: { spinnerLoader },
  data() {
    return {
      arrayReports: [],
      type_reports: "",
      isLoading: false,
      startDate: null,
      endDate: null,
    };
  },
  created() {
    this.getReports();
    moment().format("dddd, MMMM Do YYYY");
  },
  methods: {
    getReports() {
      this.isLoading = true;
      axios
        .get("http://localhost:5000/home/reports/products")
        .then((data) => {
          this.arrayReports = data.data;
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => (this.isLoading = false));
    },
    getReportsSelector(type) {
      this.isLoading = true;
      axios
        .get("http://localhost:5000/home/reports/" + type)
        .then((data) => {
          this.arrayReports = data.data;
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => (this.isLoading = false));
    },
    printFull() {
      let doc = new jsPDF("landscape");
      var imgData =
        "";

      //Coordenadas imagen
      doc.addImage(imgData, "JFIF", 7, 2, 30, 30);
      doc.setFontSize(16); //Tama√±o del letra
      doc.text("Gym", 37, 18); //derecha,izquierda(x)  arriba,abajo(y)
      doc.text("Spartans", 37, 23);

      doc.setFontSize(22);
      doc.text(`Reporte de ${this.arrayReports[0].type}`, 210, 18); //derecha,izquierda(x)  arriba,abajo(y)

      doc.setDrawColor(255, 128, 0); //Color de linea
      doc.setLineWidth(1); //Dibuja linea con grosor de 1
      doc.line(60, 19, 280, 19);

      doc.setTextColor(0, 0, 0);
      doc.setFontSize(13);
      doc.text(`Fecha de impresi√≥n ${moment().format("L")}`, 210, 38); //derecha,izquierda(x)  arriba,abajo(y)
      if (moment(this.startDate).isValid() && moment(this.endDate).isValid()) {
        doc.text(`Periodo ${this.startDate} a ${this.endDate}`, 210, 43);
      } else {
        doc.text(`Periodo de todos los registros`, 210, 43);
      }

      //autoTable(doc,{ html: '#tablita'});
      // let head = Object.keys(this.arrayReports[0]);
      let bodyformatventas;
      let bodyventas;
      let bodyformatempleado;
      let bodyempleado;
      let bodyformatproducto;
      let bodyproduct;
      let bodyformatcliente;
      let bodyclient;
      let bodyformatmaquina;
      let bodymaquina;
      let total_ventas = 0;
      let total_cuotas = 0;
      switch (this.arrayReports[0].type) {
        case "Empleado":
          bodyformatempleado = this.searchDate.map((e) => {
            let array = {};
            array["Identificador"] = e._id;
            array["Nombre"] = e.array.nombre;
            array["Apellidos"] = e.array.apellidos;
            array["Puesto"] = e.array.puesto;
            array["Fecha"] = moment(e.array.fecha).format("L");
            return array;
          });
          bodyempleado = bodyformatempleado.map((e) => Object.values(e));
          doc.autoTable({
            // html: "#tablita",
            head: [["Identificador", "Nombre", "Apellidos", "Puesto", "Fecha"]],
            body: bodyempleado,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Numero de empleados ${this.searchDate.length}`, 210, 48);
          break;
        case "Producto":
          bodyformatproducto = this.searchDate.map((e) => {
            let array = {};
            array["Identificador"] = e._id;
            array["Nombre"] = e.array.nom_producto;
            array["Descripcion"] = e.array.descripcion;
            array["Precio venta"] = e.array.precio_venta;
            array["Precio compra"] = e.array.precio_compra;
            array["Existencia"] = e.array.cant_existencia;
            return array;
          });
          bodyproduct = bodyformatproducto.map((e) => Object.values(e));
          doc.autoTable({
            // html: "#tablita",
            head: [
              [
                "Identificador",
                "Nombre",
                "Descripcion",
                "Precio venta",
                "Precio compra",
                "Existencia",
              ],
            ],
            body: bodyproduct,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Numero de productos ${this.searchDate.length}`, 210, 48);
          break;
        case "Cliente":
          bodyformatcliente = this.searchDate.map((e) => {
            let array = {};
            array["Identificador"] = e._id;
            array["Nombre"] = e.array.nombre;
            array["Genero"] = e.array.genero;
            array["Suscripcion"] = e.array.tipo_suscripcion;
            array["Tipo de pago"] = e.array.tipo_pago;
            array["Cuota"] = e.array.cuota.$numberDecimal;
            return array;
          });
          bodyclient = bodyformatcliente.map((e) => Object.values(e));
          doc.autoTable({
            // html: "#tablita",
            head: [
              [
                "Identificador",
                "Nombre",
                "Genero",
                "Suscripcion",
                "Membresia",
                "Cuota",
              ],
            ],
            body: bodyclient,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          for (const total2 of this.searchDate) {
            console.log(total2.array.cuota.$numberDecimal);
            total_cuotas += parseInt(total2.array.cuota.$numberDecimal);
          }
          doc.text(`Total de cuotas $${total_cuotas}`, 210, 53);
          doc.text(`Numero de clientes ${this.searchDate.length}`, 210, 48);
          break;
        case "Maquina":
          bodyformatmaquina = this.searchDate.map((e) => {
            let array = {};
            array["Identificador"] = e._id;
            array["Nombre"] = e.array.nombre;
            array["Descripcion"] = e.array.descripcion;
            array["Cantidad"] = e.array.cantidad;
            array["Estado"] = e.array.estado ? "Activa" : "No activa";
            return array;
          });
          bodymaquina = bodyformatmaquina.map((e) => Object.values(e));
          doc.autoTable({
            // html: "#tablita",
            head: [
              ["Identificador", "Nombre", "Descripcion", "Cantidad", "Estado"],
            ],
            body: bodymaquina,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Numero de maquinas ${this.searchDate.length}`, 210, 48);
          break;
        case "Venta":
          bodyformatventas = this.searchDate.map((e) => {
            let array = {};
            array["Identificador"] = e._id;
            array["Tipo"] = e.type;
            array["Subtotal"] = e.description;
            array["Fecha"] = moment(e.fecha).format("L");
            array["Ventas"] = e.array.map(
              (e) =>
                " NOMBRE: " +
                e.nom_producto +
                "  CANTIDAD: " +
                e.cantidad +
                "\n"
            );
            return array;
          });
          bodyventas = bodyformatventas.map((e) => Object.values(e));
          //Creaci√≥n de tabla en documento pdf
          doc.autoTable({
            // html: "#tablita",
            head: [["Identificador", "Tipo", "Subtotal", "Fecha", "Ventas"]],
            body: bodyventas,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Numero de ventas ${this.searchDate.length}`, 210, 48);

          for (const total2 of this.searchDate) {
            console.log(total2.description);
            total_ventas += parseInt(total2.description);
          }

          doc.text(`Total de ventas $${total_ventas}`, 210, 53);
          break;
      }
      doc.setTextColor(155, 155, 155);
      doc.setFontSize(13);
      doc.text("-Gym Spartans-", 135, 206); //derecha,izquierda(x)  arriba,abajo(y)
      doc.save(`ReporteTotal${this.arrayReports[0].type}.pdf`);
    },
    imprimir(type, array, total) {
      let doc = new jsPDF("landscape");
      var imgData =
        "";

      //Coordenadas imagen
      doc.addImage(imgData, "JFIF", 7, 2, 30, 30);
      doc.setFontSize(16); //Tama√±o del letra
      doc.text("Gym", 37, 18); //derecha,izquierda(x)  arriba,abajo(y)
      doc.text("Spartans", 37, 23);

      doc.setFontSize(22);
      doc.text(`Reporte de ${type}`, 210, 18); //derecha,izquierda(x)  arriba,abajo(y)

      doc.setDrawColor(255, 128, 0); //Color de linea
      doc.setLineWidth(1); //Dibuja linea con grosor de 1
      doc.line(60, 19, 280, 19);

      doc.setTextColor(0, 0, 0);
      doc.setFontSize(13);
      doc.text(`Fecha de impresi√≥n ${moment().format("L")}`, 210, 38); //derecha,izquierda(x)  arriba,abajo(y)
      doc.text(`Periodo ${moment().get("month")}`, 210, 43);

      let client, empleado, producto, maquina;
      let bodyventa;
      let bodyclient;
      let bodyempleado;
      let bodyproducto;
      let bodymaquina;
      //Creaci√≥n de tabla en documento pdf

      switch (this.arrayReports[0].type) {
        case "Cliente": {
          client = {
            Identificador: array._id,
            Nombre: array.nombre,
            Genero: array.genero,
            Suscripcion: array.tipo_suscripcion,
            Membresia: array.membresia,
            Cuota: array.cuota.$numberDecimal,
          };
          bodyclient = Object.values(client);
          doc.autoTable({
            // html: "#tablita",
            head: [
              [
                "Identificador",
                "Nombre",
                "Genero",
                "Suscripcion",
                "Membresia",
                "Cuota",
              ],
            ],
            body: [bodyclient],
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          break;
        }
        case "Venta": {
          bodyventa = array.map((e) => Object.values(e));
          doc.autoTable({
            // html: "#tablita",
            head: [
              [
                "Identificador",
                "Nombre del Producto",
                "Descripcion",
                "Subtotal",
                "Cantidad",
              ],
            ],
            body: bodyventa,
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Total $${total}`, 210, 48);
          break;
        }
        case "Empleado": {
          empleado = {
            Identificador: array._id,
            Nombre: array.nombre,
            Apellidos: array.apellidos,
            Puesto: array.puesto,
            Fecha: moment(array.fecha).format("L"),
          };
          bodyempleado = Object.values(empleado);
          doc.autoTable({
            // html: "#tablita",
            head: [["Identificador", "Nombre", "Apellidos", "Puesto", "Fecha"]],
            body: [bodyempleado],
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          break;
        }
        case "Maquina": {
          maquina = {
            Identificador: array._id,
            Nombre: array.nombre,
            Descripcion: array.descripcion,
            Cantidad: array.cantidad,
            Estado: array.estado ? "Activa" : "No activa",
          };
          bodymaquina = Object.values(maquina);
          doc.autoTable({
            // html: "#tablita",
            head: [
              ["Identificador", "Nombre", "Descripcion", "Cantidad", "Estado"],
            ],
            body: [bodymaquina],
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          doc.text(`Total $${total}`, 210, 48);
          break;
        }
        case "Producto": {
          producto = {
            Identificador: array._id,
            Nombre: array.nom_producto,
            Descripcion: array.descripcion,
            "Precio venta": array.precio_venta,
            "Precio compra": array.precio_compra,
            Existencia: array.cant_existencia,
          };
          bodyproducto = Object.values(producto);
          doc.autoTable({
            // html: "#tablita",
            head: [
              [
                "Identificador",
                "Nombre",
                "Descripcion",
                "Precio venta",
                "Precio compra",
                "Existencia",
              ],
            ],
            body: [bodyproducto],
            styles: { halign: "center", fillColor: [9, 66, 147] }, //Centrado de contenido en primera fila y color

            columnStyles: {
              0: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 1 columna
              1: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 2 columna
              2: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 3 columna
              3: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 4 columna
              4: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 5 columna
              5: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 6 columna
              6: { halign: "center", fillColor: [246, 246, 246] }, //Centrado y color en 7 columna
            },
            margin: { top: 40 }, //margin
            startY: 54, //coordenada (y) para posicionar tabla
          });
          break;
        }
        default: {
          break;
        }
      }
      doc.setTextColor(155, 155, 155);
      doc.setFontSize(13);
      doc.text("-Gym Spartans-", 135, 206); //derecha,izquierda(x)  arriba,abajo(y)
      doc.save(`Reporte${this.arrayReports[0].type}.pdf`);
    },
  },
  computed: {
    searchDate() {
      if (moment(this.startDate).isValid() && moment(this.endDate).isValid()) {
        const start = moment(this.startDate).format("L");
        const end = moment(this.endDate).format("L");

        return this.arrayReports.filter((fecha) => {
          if (start === null && end === null) {
            return this.arrayReports;
          } else {
            var data = moment(fecha.fecha).format("L");
            return data >= start && data <= end;
            // if (
            //   moment(data).get("month") == moment(start).get("month") &&
            //   moment(data).get("month") == moment(end).get("month")
            // ) {
            //   console.log(true);

            // } else {
            //   console.log(false);
            // }
          }
        });
      } else {
        return this.arrayReports;
      }
    },
  },
};
</script>

